<script>
export default {
  name: "PDISM",
  data() {
    return {
      selected_data: {
        specie: "Homo Sapiens",
        gene: null,
      },
      listed_data: {
        datatype: [
          "Homo Sapiens",
          "Mus Musculus",
        ],
        gene: [
            "Nrxn1",
            "Nrxn2",
            "Nrxn3",

        ],
      },
      demopct: null,
      is_loading: false
    }
  },
  methods: {
    submit() {
      this.is_loading = true;

      // this.demopct='test/Nrxn1.tif'
      this.$axios.get('test/Nrxn1-p.png',{ responseType: 'blob' }).then(res => {
        const blob = new Blob([res.data], { type: 'image/png' });
        this.demopct = URL.createObjectURL(blob);

      }).finally(() => {
        this.is_loading = false;
      });
      // this.is_loading=false;

    }
  },
  mounted() {
  }
}
</script>

<template>

  <v-sheet class="mt-6 pa-3" min-height="400" >
    <v-row>

      <v-col xl="2" lg="2" md="3" sm="12" cols="12">
        <v-row>
          <v-col cols="12">
            <v-select item-color="orange " hide-details label="Specie" :items="listed_data.datatype"
                      v-model="selected_data.data_type" dense
                      outlined></v-select>
          </v-col>
          <v-col cols="12">
            <v-select item-color="orange " hide-details label="Gene" :items="listed_data.gene"
                      v-model="selected_data.gene" dense outlined></v-select>
          </v-col>

          <v-col cols="12" class="d-flex justify-center">
            <v-btn @click="submit" elevation="1" color="orange lighten-3">search
              <v-icon>mdi-rocket</v-icon>
            </v-btn>
          </v-col>
        </v-row>

      </v-col>
      <v-col xl="10" lg="10" md="9" sm="12" cols="12">

        <v-sheet min-height="500">

          <v-progress-linear color="orange" v-show="is_loading" indeterminate
                             rounded></v-progress-linear>
          <v-img :src='demopct' contain min-height="400" v-show="!is_loading"></v-img>
        </v-sheet>
      </v-col>
    </v-row>

  </v-sheet>


</template>

