<script>
import Tiff from 'tiff.js'

export default {
  name: "ASG",
  data() {
    return {
      selected_data: {
        data_type: "All",
        dataset: null,
        sc_cell_type: null,
        output: null,
        software: null,
        gene: null,
        otherinfo: null
      },
      listed_data: {
        datatype: [
          "All",
          "RNA-seq",
          "scRNA-seq"
        ],
        dataset: [],
        sc_cell_type: ["All",
          "Astrocytes",
          "Endothelial",
          "Ependymal",
          "Excitatory Neurons",
          "Interneurons",
          "Microglia",
          "Oligodendrocyte",
          "Oligodendrocyte Precursor Cells (OPCs)"],
        output: ['All', "Gene expression", "Alternative Splicing"],
        software: [],
        gene: [            "Nrxn1",
          "Nrxn2",
          "Nrxn3",
        ],
        otherinfo: ["All",
          "Amino Acid Sequence",
          "Protein Domain",
          "None"]
      },
      is_otherinfo_show: false,
      is_celltype_show: false,
      demopct: null,
      is_loading: false
    }
  },
  watch: {
    'selected_data.data_type': {
      handler(newval, oldval) {
        if (newval == "All") {
          this.listed_data.dataset = ["Primary cultured cortex cells of mouse with different drug treatment (in vitro, mouse) ",
            "CA1, CA3, LEC & MEC before and after shock treatment (in vivo, mouse)",
            "BLA before and after shock treatment (in vivo, mouse)",
            "Cortical amygdala with STFP (in vivo, mouse)",
            "iN cells with drug treatment (in vitro, human)",
          ]

          this.is_celltype_show = false;
          this.listed_data.software = [
            "All",
            "rMATs",
            "Leafcutter",
            "BIRE"
          ]

        } else if (newval == "RNA-seq") {

          this.listed_data.dataset = ["Primary cultured cortex cells of mouse with different drug treatment (in vitro, mouse) ",
            "CA1, CA3, LEC & MEC before and after shock treatment (in vivo, mouse)",
          ]

          this.listed_data.software = [
            "All",
            "rMATs",
            "Leafcutter",
          ]

          this.is_celltype_show = false

        } else if (newval == "scRNA-seq") {
          this.listed_data.dataset = [
            "BLA before and after shock treatment (in vivo, mouse)",
            "Cortical amygdala with STFP (in vivo, mouse)",
            "iN cells with drug treatment (in vitro, human)",
          ]
          this.is_celltype_show = true;
          this.listed_data.software = [
            "All",
            "BIRE"
          ]
        }
      },
      immediate: true,
      deep: true
    },
    'selected_data.output': {
      handler(newval, oldval) {
        console.log(newval)
        if (newval == "Alternative Splicing") {
          this.is_otherinfo_show = true;
        }
      },
      immediate: true,
      deep: true
    }
  },
  methods: {
    submit() {
      this.is_loading = true;

      // this.demopct='test/Nrxn1.tif'
      this.$axios.get('test/Nrxn1.png',{ responseType: 'blob' }).then(res => {
        const blob = new Blob([res.data], { type: 'image/png' });
        this.demopct = URL.createObjectURL(blob);

      }).finally(() => {
        this.is_loading = false;
      });
      // this.is_loading=false;

    }
  },
  mounted() {
  }
}
</script>

<template>

  <v-sheet class="mt-6 pa-3" min-height="400" >
    <v-row>

      <v-col xl="2" lg="2" md="3" sm="12" cols="12">
        <v-row>
          <v-col cols="12">
            <v-select item-color="orange " hide-details label="Data Type" :items="listed_data.datatype"
                      v-model="selected_data.data_type" dense
                      outlined></v-select>
          </v-col>
          <v-col cols="12">
            <v-select item-color="orange " hide-details label="Dataset" :items="listed_data.dataset"
                      v-model="selected_data.dataset" dense outlined></v-select>
          </v-col>
          <v-col cols="12">
            <v-select item-color="orange " hide-details label="Outputs" :items="listed_data.output"
                      v-model="selected_data.output" dense outlined></v-select>
          </v-col>
          <v-col cols="12" v-show="is_celltype_show">
            <v-select item-color="orange " hide-details label="cell type" :items="listed_data.sc_cell_type"
                      v-model="selected_data.sc_cell_type" dense outlined></v-select>
          </v-col>
          <v-col cols="12">
            <v-select item-color="orange " hide-details label="Software" :items="listed_data.software"
                      v-model="selected_data.software" dense outlined></v-select>
          </v-col>
          <v-col cols="12">
            <v-select item-color="orange " hide-details label="Gene" :items="listed_data.gene"
                      v-model="selected_data.gene" dense outlined></v-select>
          </v-col>

          <v-col cols="12" v-show="is_otherinfo_show">
            <v-select item-color="orange " hide-details label="Other information" :items="listed_data.otherinfo"
                      v-model="selected_data.otherinfo" dense outlined></v-select>
          </v-col>

          <v-col cols="12" class="d-flex justify-center">
            <v-btn @click="submit" elevation="1" color="orange lighten-3">search
              <v-icon>mdi-rocket</v-icon>
            </v-btn>
          </v-col>
        </v-row>

      </v-col>
      <v-col xl="10" lg="10" md="9" sm="12" cols="12">

        <v-sheet min-height="500">

          <v-progress-linear color="orange" v-show="is_loading" indeterminate
                             rounded></v-progress-linear>
          <v-img :src='demopct' contain min-height="400" v-show="!is_loading"></v-img>
        </v-sheet>
      </v-col>
    </v-row>

  </v-sheet>


</template>

<style scoped lang="scss">

</style>